input[type="radio"] {
  display: none;
}
.x:checked + label {
  background-color: #000b5ed7;
  color: white;
}

//Custom property
$primary-color: rgb(27, 38, 44);
$primary-color-alpha: rgba(27, 38, 44, 0.8);

$second-color-dark: rgb(15, 76, 117);
$second-color-card: #3282b8;
$second-color-light: #bbe1fa;
$second-color-light-alpha: rgba(189, 225, 250, 0.827);

//Mixing

@mixin transition() {
  transition: all 0.4s ease-in-out;
}

//Reset style

//Bs style
.accordion-body {
  background-color: $second-color-light-alpha !important;
}
.accordion-button {
  background: $second-color-light;
}
.accordion-button:not(.collapsed) {
  color: $second-color-card !important;
}

.btn-page {
  cursor: pointer;
  color: $second-color-dark;
}
.btn-page:hover {
  color: $second-color-card;
}
.bg-body-tertiary {
  background-color: $primary-color !important;
}

.navbar-toggler {
  .navbar-toggler-icon {
    --bs-navbar-toggler-icon-bg: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%2850, 130, 184' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
    transition: all 0.3s ease-out;
  }
  .navbar-toggler-icon:hover {
    --bs-navbar-toggler-icon-bg: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%28187, 225, 250, 0.75%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
    opacity: 0.75;
  }
}

.card {
  width: 250px;
}

.carousel-control-prev-icon {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%233282b8'%3e%3cpath d='M11.354 1.646a.5.5 0 0 1 0 .708L5.707 8l5.647 5.646a.5.5 0 0 1-.708.708l-6-6a.5.5 0 0 1 0-.708l6-6a.5.5 0 0 1 .708 0z'/%3e%3c/svg%3e");
}

.carousel-control-next-icon {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%233282b8'%3e%3cpath d='M4.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L10.293 8 4.646 2.354a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e");
}

//my stile
.bg-body {
  background: $second-color-light;
}

.navbar {
  box-shadow: -1px -21px 63px -9px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: -1px -21px 63px -9px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -1px -21px 63px -9px rgba(0, 0, 0, 0.75);
  .container-fluid {
    .navbar-brand {
      color: $second-color-light;
      b {
        color: $second-color-card;
      }
    }
    .navbar-collapse {
      nav {
        .navbar-nav {
          width: 100%;
          display: flex;
          align-items: start;
          .nav-link {
            color: $second-color-light;
            text-align: start;
            font-weight: bold;

            @include transition();
          }
          .nav-link:hover {
            background-color: $second-color-card;
            color: $primary-color;
          }
        }
      }
    }
  }
}
.body {
  .filter {
    background: $second-color-light;
  }
}

.btn-filter {
  background: $second-color-card;
  color: white;
}
.btn-filter:hover {
  background: $second-color-dark;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100vw;
}

.w-60 {
  width: 60%;
}

.img-details {
  width: 250px;
}
.parrafo {
  white-space: pre-wrap;
  text-align: justify;
}
.img-chart {
  width: 250px;
}

.primary-color-alpha {
  background: $primary-color-alpha;
}
//media querys

@media (min-width: 760px) {
  .nav-link:hover {
    background-color: transparent !important;
    color: white !important;
  }
  .w-50 {
    width: 25% !important;
  }
  .img-chart {
    width: 250px;
    height: 370px;
  }
}
